// Breakpoints
$mobile: 320px;
$tablet: 768px;
$desktop: 1024px;

// Common styles
$header-gradient: rgba(0, 0, 0, 0.56);
$bg-color: #fff;
$primary-text-color: #fff;
$secondary-text-color: #000;
$accent-color: #ff6b08;
$grey-primary-color: #545454;
$grey-secondary-color: #f7f7f7;
$modal-key-color: #8c8c8c;
$attention-color: #ff001b;

// Animation
$animation-dur: 500ms;
$animation-type: cubic-bezier(0.4, 0, 0.2, 1);
