.backdrop {
  // position: fixed;
  padding: 20px;
  background-color: $header-gradient;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  @media screen and(min-width: $tablet) {
    padding: 231px 75px;
  }

  @media screen and(min-width: $desktop) {
    padding: 100px 71px;
  }
}

.film-card {
  padding: 48px 20px 41px 20px;
  background-color: $bg-color;
  position: relative;

  @media screen and(min-width: $tablet) {
    padding: 42px 30px 40px 28px;
    display: flex;
  }

  @media screen and(min-width: $desktop) {
    padding: 50px 30px 40px;
  }
}

.film-card__img {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;

  @media screen and(min-width: $tablet) {
    min-width: 264px;
    height: 374px;
    margin-right: 32px;
    margin-bottom: 0;
  }

  @media screen and(min-width: $desktop) {
    min-width: 396px;
    height: 478px;
    margin-right: 30px;
  }
}

.film-card__descr {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: $secondary-text-color;
}

.film-card__title {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.15;
  color: $secondary-text-color;
  margin-bottom: 20px;

  @media screen and(min-width: $desktop) {
    font-size: 30px;
    line-height: 1.17;
  }
}

.film-specification__list {
  margin-bottom: 20px;
}

.film-specification__item {
  display: flex;
  color: $modal-key-color;
}

.film-specification__item .symbol {
  color: $modal-key-color;
  margin: 0 3px;
}

.film-specification__item:not(:last-child) {
  margin-bottom: 10px;
}
.film-specification__name {
  min-width: 108px;
}

.film-specification__value {
  display: flex;
  color: $secondary-text-color;
  justify-content: center;
  align-items: center;
}

.film-specification__vote--light {
  width: 36px;
  height: 16px;
  color: $primary-text-color;
  background-color: $accent-color;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
}

.film-specification__vote,
.film-specification__pop {
  line-height: 1.17;
}

.description__title {
  font-style: normal;
  font-weight: 500;
  margin-bottom: 10px;
}

.description__text {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
}

.film-card__buttons {
  display: flex;
  justify-content: center;
  margin-top: 21px;

  @media screen and(min-width: $desktop) {
    justify-content: start;
  }
}

.film-card__buttons button {
  display: flex;
  width: 113px;
  height: 44px;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  align-items: center;
  text-align: center;
  justify-content: center;
  text-transform: uppercase;
  border-radius: 5px;
  border: 1px solid $secondary-text-color;

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and(min-width: $tablet) {
    width: 125px;
  }

  &:hover,
  &:focus {
    color: $primary-text-color;
    background-color: $accent-color;
    border: none;
  }
}

#addBtnWatched {
  margin-right: 15px;
  padding: 14px 8px 12px 8px;
}

#addBtnQueue {
  padding: 14px 7px 12px 7px;
}

.modal-icon {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;

  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    fill: $accent-color;
  }
}

.close-modal__button {
  border: none;
}
